	display: none !important;
	visibility: hidden;
}

/* -----------------------------------------------------------------------------
 *
 * Hide from browsers & show for screen readers
 *
 * ----------------------------------------------------------------------------- */

.screen-reader-text, /* Necessary for support WordPress support */
.u-visually-hidden {
	border: 0;
	clip: rect(0, 0, 0, 0);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	white-space: nowrap;
	width: 1px;
}

/* -----------------------------------------------------------------------------
 *
 * Show for browsers & screen readers
 *
 * ----------------------------------------------------------------------------- */

.u-visually-shown {
	clip: auto;
	height: auto;
	margin: 0;
	overflow: visible;
	position: static;
	white-space: inherit;
	width: auto;
}

