/* -----------------------------------------------------------------------------
 *
 * Styles that apply to the general block editor.
 *
 * ----------------------------------------------------------------------------- */

/* -----------------------------------------------------------------------------
 * Global Styles
 * ----------------------------------------------------------------------------- */

.editor-styles-wrapper {
	font: 400 normal 100% / 1.5 sans-serif;
	color: rgba(21, 21, 21, 1);
	background: rgba(255, 255, 255, 1);
}

/* Disable WP Core's vertical spacing. */

.editor-styles-wrapper .block-editor-block-list__block {
		margin-top: 0;
		margin-bottom: 0;
	}

/* -----------------------------------------------------------------------------
 *
 * Alignments as applied to the Block Editor
 *
 * ----------------------------------------------------------------------------- */

/* -----------------------------------------------------------------------------
 * Media Alignments
 *
 * Handle media alignments in the block editor.
 * Overrides some complex default alignments.
 * ----------------------------------------------------------------------------- */

.block-editor-block-list__layout .block-editor-block-list__block .wp-block-audio,
	.block-editor-block-list__layout .block-editor-block-list__block .wp-block-video,
	.block-editor-block-list__layout .block-editor-block-list__block .wp-block-embed,
	.block-editor-block-list__layout .block-editor-block-list__block .wp-block-image {
		margin-top: 48px;
		margin-bottom: 48px;
	}

/* CASE: Alignment: Full */

.block-editor-block-list__layout .block-editor-block-list__block[data-align="full"] {
		margin-left: auto;
		margin-right: auto;
	}

/* CASE: Alignment: Left & Right */

.block-editor-block-list__layout .block-editor-block-list__block[data-align="left"] > .is-block-content,
	.block-editor-block-list__layout .block-editor-block-list__block[data-align="right"] > .is-block-content {
		margin: 0;
	}

.block-editor-block-list__layout .block-editor-block-list__block[data-align="left"] > .is-block-content .wp-block-image, .block-editor-block-list__layout .block-editor-block-list__block[data-align="right"] > .is-block-content .wp-block-image {
			margin-top: 32px;
			margin-bottom: 32px;
		}

@media (min-width: 768px) {

.block-editor-block-list__layout .block-editor-block-list__block[data-align="left"] > .is-block-content .wp-block-image, .block-editor-block-list__layout .block-editor-block-list__block[data-align="right"] > .is-block-content .wp-block-image {
				margin-top: 5px
		}
			}

/* CASE: Image has not been resized by the editor. */

@media (min-width: 768px) {

.block-editor-block-list__layout .block-editor-block-list__block[data-align="left"] > .is-block-content .wp-block-image:not(.is-resized), .block-editor-block-list__layout .block-editor-block-list__block[data-align="right"] > .is-block-content .wp-block-image:not(.is-resized) {
					max-width: 480px
			}
				}

/* CASE: Alignment: Left */

@media (min-width: 768px) {

.block-editor-block-list__layout .block-editor-block-list__block[data-align="left"] > .is-block-content .wp-block-image {
				float: left;
