/* -----------------------------------------------------------------------------
 *
 * WP Core Block Styles & Overrides
 *
 * Styles that apply to WP Core blocks or reset styles applied
 * by the block editor and wp-admin.
 *
 * ----------------------------------------------------------------------------- */

[data-type^="core"] {
	/* -----------------------------------------------------------------------------
	 * Block Editor Kitchen Sink Grid Widths
	 * ----------------------------------------------------------------------------- */

	max-width: 792px;
	width: calc(100% - 48px);
}

@media (min-width: 768px) {

[data-type^="core"] {
		width: calc(100% - 80px)
}
	}

[data-type^="core"][data-align="wide"] {
		max-width: 1000px;
	}

[data-type^="core"][data-align="full"] {
		max-width: 1600px;
	}

/* CASE: core blocks within a block should be reset. */

.c-block [data-type^="core"] {
		width: 100%;
		max-width: none;
	}

/* -----------------------------------------------------------------------------
	 * Horizontal Rule/Separator
	 *
	 * Override WP-Core's heavy-selector max-width value.
	 * ----------------------------------------------------------------------------- */

[data-type^="core"] hr {
		max-width: none !important;
	}

/* -----------------------------------------------------------------------------
